<template>
  <div class="music-controls">
    <span class="star">{{ newCurrentTime }}</span>
    <div class="contro">
      <label>
        <input
          class="bar"
          type="range"
          name="buffer"
          :max="
            newplayvoice?.duration
              ? newplayvoice?.duration
              : newplayvoice?.voiceInfo?.duration
          "
          id=""
          min="0"
          :value="tmpCurrentTime"
          @input="
            draging = true;
            updateBar($event.target.value, duration);
          "
          @change="
            draging = false;
            changecurrentTime($event.target.value);
          "
        />

        <div class="buffer">
          <span class="bar" ref="span"></span>
          <div class="point" ref="point">
            <div class="dot" ref="dot"></div>
          </div>
        </div>
        <!--:style="{ width: newwidth + '%' }" -->
      </label>
    </div>
    <span class="end" v-if="newplayvoice?.duration">{{ totaltime }}</span>
    <span class="end" v-else>{{ durationMin }}{{ durationSec }}</span>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  props: {
    newplayvoice: Object,
    userinfo: Object,
  },
  data: function () {
    return {
      //   showLyric: true,
      draging: false,
      tmpCurrentTime: this.currentTime,
      newwidth: 0,
      newCurrentTime: "00:00",
      updatetime: 0,
      totaltime:
        String(Math.floor(this.newplayvoice?.duration / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(this.newplayvoice?.duration % 60)).padStart(2, "0"),
    };
  },
  computed: {
    durationMin: function () {
      return Math.floor(
        this.newplayvoice?.voiceInfo?.duration
          ? this.newplayvoice?.voiceInfo?.duration
          : this.userinfo?.userVoice?.voiceInfo?.duration / 60
      ) < 10
        ? "0" +
            Math.floor(
              this.newplayvoice?.voiceInfo?.duration
                ? this.newplayvoice?.voiceInfo?.duration
                : this.userinfo?.userVoice?.voiceInfo?.duration / 60
            ) +
            ":"
        : Math.floor(
            this.newplayvoice?.voiceInfo?.duration
              ? this.newplayvoice?.voiceInfo?.duration
              : this.userinfo?.userVoice?.voiceInfo?.duration / 60
          ) + ":";
    },
    durationSec: function () {
      return Math.floor(
        this.newplayvoice?.voiceInfo?.duration
          ? this.newplayvoice?.voiceInfo?.duration
          : this.userinfo?.userVoice?.voiceInfo?.duration % 60
      ) < 10
        ? "0" +
            Math.floor(
              this.newplayvoice?.voiceInfo?.duration
                ? this.newplayvoice?.voiceInfo?.duration
                : this.userinfo?.userVoice?.voiceInfo?.duration % 60
            )
        : Math.floor(
            this.newplayvoice?.voiceInfo?.duration
              ? this.newplayvoice?.voiceInfo?.duration
              : this.userinfo?.userVoice?.voiceInfo?.duration % 60
          );
    },
    ...mapState(["duration", "currentTime"]),
  },
  methods: {
    updateBar(timing, timed) {
      let barlength = (timing / timed) * 100 + "%";
      this.$refs.span.style.width = barlength;
      this.$refs.point.style.left = barlength;
      this.$refs.dot.style.left = barlength / 2;
    },
    ...mapMutations(["changeduration", "changecurrentTime"]),
  },
  mounted() {
    // let barlength = Math.ceil((this.currentTime / this.duration) * 100) + "%";
    let barlength = (this.currentTime / this.duration) * 100 + "%";
    this.$refs.span.style.width = barlength;
    this.$refs.point.style.left = barlength;
    this.$refs.dot.style.left = barlength / 2;
  },
  watch: {
    currentTime: function (n) {
      // console.log(n);
      if (!this.draging) {
        this.tmpCurrentTime = n;
        this.updateBar(this.tmpCurrentTime, this.duration);
      }
      // this.newwidth = (n / this.duration) * 100;
      this.newCurrentTime =
        String(Math.floor(n / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(n % 60)).padStart(2, "0");
      // console.log(this.newwidth);
    },
    updatetime(updatetime) {
      // console.log(updatetime);
      this.tmpCurrentTime = updatetime;
    },
  },
};
</script>

<style lang="less">
.music-controls {
  display: flex;
  align-items: center;
  padding: 0 3rem;
  z-index: 9;
  .contro {
    // padding: 0 5rem;
    box-sizing: border-box;
    label {
      position: relative;
      display: block;
      input.bar {
        width: 225rem;
        margin: 0;
        height: 5rem;
        display: block;
        opacity: 0;
        z-index: 9;
      }
      .buffer {
        width: 225px;
        height: 3px;
        background-color: #e5e5e5;
        display: flex;
        align-items: center;
        border-radius: 10px;
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        span.bar {
          height: 3rem;
          display: block;
          // width: 100rem;
          border-radius: 5px;
          // background-color: rgb(103, 167, 226);
          //  height: 5rem;
          // width: 100%;
          background: #fe5353;
          // border-radius: 0.03rem;
          position: absolute;
        }
        .point {
          position: absolute;
          top: -19rem;
          margin-left: -20rem;
          width: 40rem;
          height: 40rem;
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 99;
          will-change: auto;
          box-sizing: border-box;

          .dot {
            width: 8px;
            height: 8px;
            position: relative;
            top: 1px;
            border-radius: 999px;
            box-shadow: 0 0 0 4px #fe5353;
            background: #fff;
          }
        }
      }
    }
  }
  span.star {
    width: 34rem;
    margin: 16rem;
    font-size: 12rem;
    color: rgba(0, 0, 0, 0.5);
    // color: #fff;
  }
  span.end {
    width: 34rem;
    margin: 16rem;
    font-size: 12rem;
    color: rgba(0, 0, 0, 0.5);
    // color: #fff;
  }
}
</style>>
