<template>
  <div class="second_channel">
    <!-- <van-sticky> -->
      <div class="tabs">
        <a
          href="#"
          class="tabs_item"
          @click.prevent="
            sort = 'smart';
            getSoloList();
          "
          :class="{ active: sort === 'smart' }"
          >智能排序</a
        >
        <a
          href="#"
          class="tabs_item"
          @click.prevent="
            sort = 'playCount';
            getSoloList();
          "
          :class="{ active: sort === 'playCount' }"
          >最多收听</a
        >
        <a
          href="#"
          class="tabs_item"
          @click.prevent="
            sort = 'uploadTime';
            getSoloList();
          "
          :class="{ active: sort === 'uploadTime' }"
          >最新上传</a
        >
      </div>
    <!-- </van-sticky> -->
    <div class="connent">
      <ul class="connent_list">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
        >
          <SortList
            v-for="(sortlist, index) in sortlists"
            :key="index"
            :sortlist="sortlist"
          />
        </van-list>
      </ul>
    </div>
    <!-- <h1>111</h1> -->
  </div>
</template>

<script>
import SortList from "@/components/SortList.vue";
import { mapState } from "vuex";
import qs from "qs";

export default {
  components: {
    SortList,
  },
  data: function () {
    return {
      sort: JSON.parse(localStorage.getItem("sort")) || "smart",
      fication:
        JSON.parse(localStorage.getItem("classification")) ||
        this.classification,
      sortlists: null,

      page: 0,
      list: [],
      loading: false,
      finished: false,
      totale: 0,
      // classo:"smart"
    };
  },
  computed: {
    ...mapState(["classification", "classort"]),
  },
  created() {
    this.getSoloList();
    this.getSoList();

  },
  methods: {
    getSoList() {
      this.axios
        .post(
          "https://m.lizhi.fm/vodapi/m5/program",
          qs.stringify({
            page: this.page,
            label: this.classification
              ? this.classification?.channelName
              : this.fication?.channelName,
            // label:"心灵治愈",
            count: 30,
            sort: this.sort,
          })
        )
        .then((res) => {
          // console.log(res);
          // this.total += 10;
          // this.totale += rows.length;
          this.sortlists=res.data.data;

        });
    },
    getSoloList() {
      // this.totale += 20;

      this.axios
        .post(
          "https://m.lizhi.fm/vodapi/m5/program",
          qs.stringify({
            page: this.page,
            label: this.classification
              ? this.classification?.channelName
              : this.fication?.channelName,
            // label:"心灵治愈",
            count: 30,
            sort: this.sort,
          })
        )
        .then((res) => {
          // console.log(res);
          let rows = res.data.data;
          // this.total += 10;
          this.loading = false;
          // this.totale += rows.length;
          this.totale = rows.length;
          // this.sortlists=res.data.data;
          if (rows == null || rows.length === 0) {
            // 加载结束
            this.finished = true;
            return;
          }
          this.sortlists = this.sortlists ? [...this.sortlists, ...rows] : rows;
          if (this.sortlists.length > this.totale) {
            this.finished = true;
          }
        });
    },
    onLoad() {
      this.page++;
      this.getSoloList();
      // this.totale += 20;
    },
  },
  watch: {
    classification: function (classification) {
      // console.log(11);
      localStorage.setItem("classification", JSON.stringify(classification));
      // this.getSoloList();
      this.getSoList();
    },
    sort: function (sort) {
      // console.log(11);
      this.sortlists=null,
      this.totale = 0;
      localStorage.setItem("sort", JSON.stringify(sort));
      // this.getSoloList()
    },
  },
};
</script>

