<template>
  <li class="item_guess">
    <div class="img">
      <van-image width="60" height="60" lazy-load class="iten_img" :src="everylist?.cover" alt="" />
      <div class="play_icon_mask">
        <div class="play_icon_inner">
          <img src="../assets/play_icon.png" alt="" />
        </div>
      </div>
    </div>
    <div class="desc">
      <h6 class="nameli">
        {{ everylist?.name }}
      </h6>
      <p>{{ everylist?.userName }}</p>
      <div class="data">
        <div class="voice_tip">
          <span>
            <div class="clock">
              <span> {{ durationMin }}{{ durationSec }} </span>
            </div>
          </span>
          <span>
            <div class="play_circl">
              <span> {{ playCount }}播放 </span>
            </div>
          </span>
          <span>
            <div class="msg">
              <span> 评论{{ everylist?.commnetCount }} </span>
            </div>
          </span>
        </div>
        <div class="opeap">{{ everylist?.label }}</div>
      </div>
    </div>
  </li>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  props: {
    everylist: [Object, Array],
  },
  computed: {
    playCount: function () {
      return this.everylist.playCount > 100000000
        ? Math.round(this.everylist.playCount / 10000000) / 10 + "亿"
        : this.everylist.playCount > 10000
        ? Math.round(this.everylist.playCount / 1000) / 10 + "万"
        : this.everylist.playCount;
    },
    durationMin: function () {
      return Math.floor(this.everylist?.duration / 60) < 10
        ? "0" + Math.floor(this.everylist?.duration / 60) + "'"
        : Math.floor(this.everylist?.duration / 60) + "'";
    },
    durationSec: function () {
      return Math.floor(this.everylist?.duration % 60) < 10
        ? "0" + Math.floor(this.everylist?.duration % 60) + "''"
        : Math.floor(this.everylist?.duration % 60) + "''";
    },
    ...mapState(["playvoice", "playsortlist"]),
  },
  methods: {
    ...mapMutations([
      "changeplaying",
      "changeplayvoice",
      "changesortlist",
      "changevoiceId",
    ]),

  },
  watch: {
    playvoice: function () {
      localStorage.setItem("playvoice", JSON.stringify(this.playvoice));
    },
  },
};
</script>

<style lang="less">
li.item_guess {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 90rem;
  position: relative;
  border-bottom: 1rem solid #ddd;
  &:last-of-type {
    border: none;
  }
  .img {
    display: block;
    width: 60rem;
    height: 60rem;
    margin-right: 16rem;
    transition: opacity 0.3s;
    overflow: hidden;
    position: relative;
    img.iten_img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 8rem;
    }
    .play_icon_mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 10000;
      display: flex;
      justify-content: center;
      align-items: center;
      .play_icon_inner {
        width: 40rem;
        height: 40rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          display: block;
          width: 8.5vw;
        }
      }
    }
  }
  .desc {
    flex: 1;
    width: 250rem;
    h6.nameli {
      display: block;
      width: 100%;
      text-align: left;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 14rem;
      color: #4c4c4c;
    }
    p {
      display: block;
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 10rem;
      text-align: left;
      color: #b2b2b2;
      padding: 3.5rem;
    }
    .data {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .voice_tip {
        font-size: 12rem;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        color: rgba(0, 0, 0, 0.3);

        span {
          display: block;
          margin-right: 6rem;

          div {
            display: flex;
            align-items: center;
            justify-content: center;
            img {
              display: block;
              width: 16rem;
              padding-right: 5rem;
            }
            span {
              display: block;
            }
          }
        }
      }
      .opeap {
        font-size: 12rem;
        float: right;
        background-color: #f3f3f3;
        color: #333;
        border-radius: 10rem;
        padding: 0 6rem;
        height: 20rem;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}
</style>>
