<template>
  <div class="details">
    <div class="details_head">
      <a class="main" href="" @click.prevent="back()">
        <img class="hom" src="../assets/home.png" alt=""
      /></a>
      <h1>
        {{ category ? category?.channelName : categorychan?.channelName }}
      </h1>
    </div>
    <van-sticky :offset-top="58" :z-index="9999">
      <div class="navv_tab">
        <router-link
          class="tab_item"
          :to="{ path: '/channeldetails?id=' + categorychan?.channelId }"
          @click.prevent="
            changID(categorychan?.channelId);
            replace();
            changeClasslists(recomchange[0]?.channelName);
            shows = true;
          "
        >
          {{ recomchange[0]?.channelName }}
        </router-link>
        <router-link
          class="tab_item"
          :to="{ path: '/channeldetails/classlsList/' + tablist?.channelId }"
          v-for="(tablist, index) in tablistes?.length < 1 ? table : tablistes"
          :key="index"
          @click.native="
            changID(tablist?.channelId);
            changeClasslists(tablist);
            shows = false;
          "
        >
          {{ tablist?.channelName }}
        </router-link>
      </div>
    </van-sticky>
    <div class="warp" v-if="$route.meta.showNav">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <RecomendSheet
          v-for="(item, index) in list"
          :key="index"
          :item="item"
        />
      </van-list>
    </div>
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive">
        <!-- <a> <router-link to="/channeldetails"></router-link></a> -->
      </router-view>
    </keep-alive>

    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <VoicePlayGlo />
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import RecomendSheet from "@/components/RecomendSheet.vue";
import VoicePlayGlo from "@/components/VoicePlayGlo.vue";

export default {
  props: {
    // item: Object,
    // recom:Array
  },
  components: {
    RecomendSheet,
    VoicePlayGlo,
  },
  data() {
    return {
      recomchange: JSON.parse(localStorage.getItem("recom")) || this.recom,
      categorychan:
        JSON.parse(localStorage.getItem("category")) || this.category,
      tablistes: JSON.parse(localStorage.getItem("tablists")) || this.tablists,
      table: JSON.parse(localStorage.getItem("tablists")),
      id: JSON.parse(localStorage.getItem("id")),
      shows: true,
      showid: 0,

      recommends: [],
      list: [],
      loading: false,
      finished: false,
      total: 0,
    };
  },
  computed: {
    ...mapState(["recom", "category", "tablists", "classification"]),
  },
  methods: {
    back() {
      this.$router.replace("/");
    },
    getTableList(id) {
      this.axios
        .get("https://m.lizhi.fm/vodapi/m5/channelTags", {
          params: { channelId: id },
        })
        .then((res) => {
          // console.log(res);
          // this.tablistes = [...this.recomchange, ...res.data.data];
          this.tablistes = res.data.data;
          // console.log(this.tablists);
          this.changeTablists(this.tablistes);
          // setTimeout( ()=> {
          //   window.location.reload();
          // }, 100);
        });

      // window.location.reload()
      // localStorage.setItem("tablists", JSON.stringify(this.tablistes));
    },
    replace() {
      if (this.recomchange[0]?.channelId != this.tablist?.channelId) {
        // this.$router.replace("/channeldetails");
        this.$router.replace({
          path: "/channeldetails/",
          query: { id: this.categorychan.channelId },
        });
        // console.log(11);
        // this.id=this.categorychan.channelId
      }
    },
    onLoad() {
      // 异步更新数据
      // this.loading = true;
      this.total += 6;
      this.axios
        .post("https://m.lizhi.fm/vodapi/playSheetRecommend/recommend", {
          params: {
            sceneCode: "emotion",
            bizCode: "playlist",
            refresh: 1,
            channelId: this.recomchange[0].channelId,
          },
        })
        .then((res) => {
          // console.log(res);
          // this.recommend = res.data.data;
          res.data.data.cardPlayBeanList.forEach((item) => {
            this.list.push(item);
          });
          if (this.list.length > this.total) {
            this.finished = true;
          }
          this.loading = false;
        });
    },
    xxx() {
      console.log(11);
      this.shows = this.categorychan?.channelId;
      // this.$router.push({
      //   path: "/channeldetails",
      //   query: { id: id },
      // });
      // console.log(this.id);
    },
    changID(id) {
      // console.log(11);
      // console.log(id);
      // this.shows = this.categorychan?.channelId;
      this.id = id;
      this.showid = id;

      // console.log(this.id);
    },
    ...mapMutations(["changeTablists", "changeClasslists"]),
  },
  created() {
    this.axios
      .post("https://m.lizhi.fm/vodapi/playSheetRecommend/recommend", {
        params: {
          sceneCode: "emotion",
          bizCode: "playlist",
          refresh: 1,
          channelId: this.recomchange[0].channelId,
        },
      })
      .then((res) => {
        // console.log(res);
        this.recommends = res.data.data.cardPlayBeanList;
      });
    if (
      this.$route.query.id != this.category?.channelId ||
      this.$route.query.id != this.categorychan.channelId
    ) {
      this.getTableList(this.categorychan.channelId);
    } else {
      this.getTableList(this.$route.query.id);
    }
    this.showid = this.categorychan?.channelId;

  },
  // beforeRouteUpdate(to, from, next) {
  //   // console.log(333);log

  //   console.log(to.query.id);
  //   console.log(from.query.id);
  //   // console.log(next.query.id);

  //   this.getTableList(to.query.id);
  //   next();
  // },
  beforeRouteUpdate(to, from, next) {
    this.id = to.params.id;
    next();
  },
  // beforeRouteLeave (to, from, next) {
  //   console.log(to.query.id);
  //   console.log(from.query.id);
  //   next()
  //   // 导航离开该组件的对应路由时调用
  //   // 可以访问组件实例 `this`
  // },
  watch: {
    category: function (category) {
      // console.log(11);
      localStorage.setItem("category", JSON.stringify(category));
      localStorage.setItem("recom", JSON.stringify(this.recom));
      // localStorage.setItem("tablists", JSON.stringify(this.tablistes));
    },
    tablistes: function (tablists) {
      // console.log(11);
      localStorage.setItem("tablists", JSON.stringify(tablists));
    },
    id(id) {
      this.shows = id;
      // console.log(this.show);
      if (id) {
        localStorage.setItem("id", JSON.stringify(id));
      }
    },
    showid(n) {
      // console.log(n);
      // console.log(o);
      localStorage.setItem("showid", JSON.stringify(n));
    },
    // $route() {
    // console.log(11);
    // console.log(this.id);
    // window.location.reload(); //监测到路由发生跳转时刷新一次页面
    // },
  },
};
</script>
