<template>
  <div class="voic_sheet" v-if="item.voiceDataList.length">
    <a href="#" class="cmd_hoc">
      <h3 class="title">
        <span class="single_dots">{{ item.playSheetInfo.name }}</span>
        <i
          class="right_arrow"
          @click.prevent="
            $router.push({
              path: '/voicesheet',
              query: { id: item.playSheetInfo.id },
            })
          "
        >
          <img src="../assets/more_icon.png" alt="" />
        </i>
      </h3>
    </a>

    <div class="sheet_list">
      <div
        class="sheet_item"
        v-for="(list, index) in item.voiceDataList"
        :key="index"
      >
        <van-image width="96" height="96" lazy-load class="cover" :src="list?.cover" alt="" />
        <div
          class="mask"
          @click="
            changeplayvoice(list);
            play =!play;
            changeplaying(play);
            changevoicelist(item.voiceDataList)
          "
        >
          <div class="play_icon_mask">
            <div class="play_icon">
              <img
                src="../assets/zhanting.png"
                alt=""
                v-if="playvoice?.voiceId === list?.voiceId && playing"
              />
              <img src="../assets/play_icon.png" v-else alt="" />
            </div>
          </div>
          <div class="play_icon_count">
            <i class="play_icon">
              <img src="../assets/play.png" alt="" />
            </i>
            <span>{{
              list.playCount > 10000
                ? Math.round(list.playCount / 1000) / 10 + "万"
                : list.playCount
            }}</span>
          </div>
        </div>
        <a href="" class="desc">{{ list.name }}</a>
      </div>
      <div
        class="sheet_more"
        @click.prevent="
          $router.push({
            path: '/voicesheet',
            query: { id: item.playSheetInfo.id },
          })
        "
      >
        <img src="../assets/more_bg.png" alt="" class="cover_more" />
        <div class="mask_more">
          <div class="see">
            <i class="list_music">
              <img src="../assets/list.png" alt="" />
            </i>
            <span>{{ item.playSheetInfo.voiceNum }}</span>
          </div>
          <div class="all_see">
            <span>查看全部</span>
            <i class="arrow">
              <img src="../assets/more_smicon.png" alt="" />
            </i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  props: {
    item: Object,
  },
  data() {
    return {
      play: false,
    };
  },
  computed: {
    ...mapState(["playing", "playvoice"]),
  },
  methods: {
    // playing(list){
    //   console.log(list);
    // },
    ...mapMutations(["changeplayvoice", "changeplaying","changevoicelist"]),
  },
};
</script>

<style lang="less">
.voic_sheet {
  overflow-y: hidden;
  display: block;
  height: 220rem;
  width: 100%;
  a {
    color: #333;
  }
  a.cmd_hoc {
    h3.title {
      font-size: 18rem;
      font-weight: 700;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 24rem 6rem 16rem 16rem;
      span.single_dots {
        display: block;
        // width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      i.right_arrow {
        img {
          width: 20rem;
          display: block;
        }
        font-size: 19rem;
        color: #a09f9f;
        font-weight: 200;
      }
    }
  }
  .sheet_list {
    display: flex;
    height: 155rem;
    width: auto;
    padding-left: 16rem;
    overflow-x: scroll;
    &::-webkit-scrollbar {
      display: none;
    }
    .sheet_item {
      height: 140rem;
      width: 96rem;
      margin-right: 16rem;
      position: relative;
      border-radius: 4rem;
      flex-shrink: 0;
      img.cover {
        width: 96rem;
        height: 96rem;
        display: block;
        border-radius: 4rem;
      }
      .mask {
        position: absolute;
        top: 0;
        left: 0;
        width: 96rem;
        height: 96rem;
        border-radius: 4rem;
        font-family: lizhifm;
        .play_icon_mask {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          z-index: 10000;
          display: flex;
          justify-content: center;
          align-items: center;
          .play_icon {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 40rem;
            height: 40rem;
            border-radius: 50%;
            img {
              display: block;
              width: 8.5vw;
              object-fit: cover;
            }
          }
        }
        .play_icon_count {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 20rem;
          z-index: 100;
          border-radius: 0 0 4rem 4rem;
          background: linear-gradient(180deg, transparent, rgba(0, 0, 0, 0.5));
          overflow: hidden;
          color: #fff;
          display: flex;
          align-items: center;
          justify-content: flex-start;
          padding-left: 3rem;
          font-size: 10rem;
          i.play_icon {
            font-size: 12rem;
            margin-right: 2rem;
            position: relative;
            top: 0.5rem;
            img {
              display: block;
              width: 20rem;
            }
          }
        }
      }
      a.desc {
        width: 91rem;
        font-size: 14rem;
        font-weight: 500;
        line-height: 20rem;
        margin: 0;
        height: 40rem;
        text-align: left;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        position: relative;
        top: 8rem;
      }
    }
    .sheet_more {
      height: 96rem;
      width: 96rem;
      position: relative;
      margin-right: 16rem;
      display: block;
      flex-shrink: 0;
      img.cover_more {
        display: block;
        height: 96rem;
        width: 96rem;
        object-fit: cover;
      }
      .mask_more {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 96rem;
        height: 96rem;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        font-size: 15rem;
        padding-left: 10rem;
        padding-top: 20rem;
        overflow: hidden;
        .see {
          height: 40rem;
          display: flex;
          align-items: center;
          border-bottom: 1rem solid hsla(0, 0%, 100%, 0.2);
          font-size: 16rem;
          i.list_music {
            width: 25rem;
            margin-right: 2.5rem;
            img {
              display: block;
              width: 25rem;
            }
          }
        }
        .all_see {
          margin-top: 6.5rem;
          font-weight: 200;
          display: flex;
          align-items: center;
          font-size: 14rem;

          i.arrow {
            img {
              width: 20rem;
              display: block;
            }
          }
        }
      }
    }
  }
}
</style>>

