<template>
  <div class="voicesheet">
    <div class="sheet_info">
      <div
        class="bg"
        :style="
          voicelist?.playSheetInfo &&
          `background-image: url(${voicelist?.playSheetInfo?.cover})`
        "
      ></div>
      <div class="cover">
        <div class="img">
          <van-image width="96" height="96" class="img_co" :src="voicelist?.playSheetInfo?.cover" alt="" />
          <div class="zi1"></div>
          <div class="zi2"></div>
        </div>
        <div class="desc">
          <h1 class="sheettitle">{{ voicelist?.playSheetInfo?.name }}</h1>
          <div
            class="userinfo"
            @click.prevent="
              $router.push({
                path: '/personalpage',
                query: { id: voicelist?.playSheetInfo?.userId },
              })
            "
          >
            <span>{{ voicelist?.playSheetInfo?.owner }}</span>
            <i class="icon">
              <img src="../assets/more_smicon.png" alt="" />
            </i>
          </div>
        </div>
      </div>
    </div>
    <div class="cmd_hoc">打开APP领取声优福利</div>
    <div class="list_title">
      <div class="playicon">
        <i class="play_cir">
          <img src="../assets/bofang.png" alt="" />
        </i>
        <span>全部播放</span>
      </div>
      <div class="total">
        (共{{ voicelist?.playSheetInfo?.voiceNum }}个声音)
      </div>
    </div>
    <div class="list">
      <ul class="voiclist">
          <VoiceList
            v-for="(voices, index) in voices"
            :key="index"
            :voices="voices"
          />
      </ul>
    </div>
    <div class="to_top" v-if="shallow" @click="backtop()">
      <div class="overline">
        <img src="../assets/top.png" alt="" />
      </div>
    </div>
    <div class="bottom" v-if="!n && voictotal >= 10">
      <!-- <div class="bottom" v-if="voictotal <= voicenumber && voictotal >= 10"> -->
      <!-- <div class="bottom" v-if="voictotal <= voicenumber && voictotal >= 10"> -->
      <div class="down" @click="getMore()">
        <img src="../assets/xialagengdou.png" alt="" />
      </div>
      <div class="cmd_hoc">打开APP收听更多声音</div>
    </div>
    <div class="dobottom" v-else>别拉了，到底啦o(╥﹏╥)o</div>
  </div>
</template>

<script>
import VoiceList from "@/components/VoiceList.vue";

export default {
  components: {
    VoiceList,
  },

  data: function () {
    return {
      voicelist: null,
      shallow: false,
      scrollTop: 0,

      voices: null,

      page: 1,
      count: 10,
      btnshow: true,
      voictotal: 0,
      voicenumber: 0,
      n: null,

    };
  },

  mounted() {
    // 开启滚动监听
    window.addEventListener("scroll", this.scrollToTop);

    // console.log(11);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },

  methods: {
    getVoiceList: function (id) {
      this.axios
        .get("https://m.lizhi.fm/vodapi/playsheet/data", {
          params: { id, page: this.page, count: this.count },
        })
        .then((res) => {
          // console.log(res);
          // console.log(res.data.voices.length);
          // this.voicenumber=res.data.voices.length
          this.voicelist = this.voicelist ? this.voicelist : res.data;
          this.voicenumber = this.voicelist?.playSheetInfo?.voiceNum;
          this.voices = this.voices
            ? [...this.voices, ...res.data.voices]
            : [...res.data.voices];
          // this.voictotal = this.voices.length;
        });
    },

    getMore() {
      this.page++;
      this.getVoiceList(this.$route.query.id);
    },
    scrollToTop() {
      this.scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      // console.log(this.scrollTop);
      if (this.scrollTop >= 180) {
        this.shallow = true;
        // document.querySelector('.van-nav-bar').style.display = 'none'
      } else {
        this.shallow = false;
        // document.querySelector('.van-nav-bar').style.display = 'block'
      }
    },
    backtop() {
      // console.log(11);
      // document.documentElement.scrollTop = 0;
      const timeTop = setInterval(() => {
        document.documentElement.scrollTop = this.scrollTop -= 30;
        if (this.scrollTop <= 0) {
          clearInterval(timeTop);
        }
      }, 10);
    },
  },
  created: function () {
    // console.log(this.$route);
    // console.log(this.$route.query.id);
    this.getVoiceList(this.$route.query.id);
  },
  watch: {
    voices: function () {
      // console.log(n);
      // console.log(o);
      this.voictotal = this.voices.length;
      // console.log( this.voictotal);
      this.n = this.voictotal >= this.voicenumber ? true : false;
    },
  },
  beforeRouteUpdate(to, from, next) {
    this.getVoiceList(to.query.id);
    next();
  },
};
</script>

<style lang="less">
.voicesheet {
  padding-top: 58rem;

  .sheet_info {
    width: 100%;
    height: 148rem;
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 16rem;
    opacity: 1;
    background: rgba(0, 0, 0, 0.71);
    overflow: hidden;
    .bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      background-size: 100% 100%;
      filter: blur(20px);
    }
    .cover {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      max-width: 100%;
      height: 100%;
      z-index: 12;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 15rem;
      background: rgba(0, 0, 0, 0.161);
      .img {
        flex: 0 0 96rem;
        width: 96rem;
        height: 96rem;
        border-radius: 50%;
        margin-right: 28rem;
        box-shadow: 0 0 5px rgb(0 0 0 / 13%);
        position: relative;
        top: 10rem;
        img.img_co {
          border-radius: 4rem;
          display: block;
          width: 100%;
          height: 96rem;
          // z-index: 9;
        }
        .zi1 {
          position: absolute;
          width: 100%;
          height: 100%;
          background-color: hsla(0, 0%, 100%, 0.2);
          border-radius: 4rem;
          z-index: -1;
          top: -6rem;
          left: 6rem;
        }
        .zi2 {
          position: absolute;
          width: 100%;
          height: 100%;
          background-color: hsla(0, 0%, 100%, 0.2);
          border-radius: 4rem;
          z-index: -1;
          top: -12rem;
          left: 12rem;
        }
      }
      .desc {
        flex: 1;
        width: 0;
        color: #fff;
        text-shadow: 0 1px 2px rgb(0 0 0 / 10%);
        h1.sheettitle {
          text-align: left;
          font-size: 18rem;
          text-shadow: 0 1px 2px rgb(0 0 0 / 10%);
        }
        .userinfo {
          display: flex;
          margin-top: 4rem;
          font-size: 13rem;
          align-items: center;
          text-shadow: 0 1px 2px rgb(0 0 0 / 10%);
          color: #fff;
          span {
            display: block;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            word-break: break-all;
            line-height: 18rem;
            // vertical-align: middle;
          }
          i.icon {
            img {
              display: block;
              width: 15rem;
            }
          }
        }
      }
    }
  }
  .cmd_hoc {
    margin: 15rem auto;
    width: 275rem;
    height: 40rem;
    border-radius: 20rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14rem;
    letter-spacing: 2rem;
    color: #fff;
    background: #fe5656;
    font-weight: 500;
    box-shadow: 0 2rem 4rem 0 rgb(255 98 98 / 30%);
  }
  .list_title {
    height: 50rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #b2b2b2;
    font-size: 14rem;
    position: relative;
    width: 345rem;
    margin: 0 auto 10rem;
    &:after {
      position: absolute;
      content: "";
      display: block;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: #dbdbdb;
      z-index: 100;
      transform: scaleY(0.5);
    }
    .playicon {
      height: 35rem;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #000;
      font-weight: 400;
      font-size: 16rem;

      i.play_cir {
        display: block;
        img {
          display: block;
          width: 25rem;
          margin: 0;
        }
      }
      span {
        display: block;
        padding: 0 6rem;
      }
    }
    .total {
      height: 35rem;
      display: flex;
      align-items: center;
      color: #b2b2b2;
      font-size: 12rem;
    }
  }
  .list {
    padding: 0 16rem;
    background-color: #fff;
    ul.voiclist {
    }
  }
  .to_top {
    position: fixed;
    z-index: 10000;
    bottom: 15rem;
    right: 15rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    .overline {
      img {
        display: block;
        width: 40rem;
        height: 40rem;
      }
    }
  }
  .bottom {
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100rem;
    position: relative;
    padding-top: 25rem;
    .down {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background: linear-gradient(0deg, #fff, hsla(0, 0%, 100%, 0.3));
      display: flex;
      justify-content: center;
      align-items: flex-start;
      padding-top: 5rem;
      img {
        display: block;
        width: 30rem;
      }
    }
  }
  .dobottom {
    height: 100rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    color: #666;
    font-size: 14rem;
  }
}
</style>

