<template>
  <li
    class="item_sort"
    @click.prevent="
      $router.push({
        path: '/voiceplaydeta',
        query: { id: sortlist?.voiceInfo.voiceId },
      });
      changeplayvoice(sortlist);
      changevoiceId(sortlist?.voiceInfo.voiceId);
      save()
      changeplaying(pay)
    "
  >
    <div class="img">
      <van-image width="60" height="60" lazy-load  class="iten_img" :src="sortlist?.userInfo?.photo" alt="" />
    </div>
    <div class="desc">
      <h3 class="nameli">
        {{ sortlist?.voiceInfo.name }}
      </h3>
      <p>{{ sortlist?.userInfo.name }}</p>
      <div class="voice_tip">
        <span>
          <div class="clock">
            <img src="../assets/icon_ls2.png" alt="" />
            <span> {{ durationMin }}{{ durationSec }} </span>
          </div>
        </span>
        <span>
          <div class="play_circl">
            <img src="../assets/bofang_icon.png" alt="" />
            <span>
              {{ playCount }}
            </span>
          </div>
        </span>
        <span>
          <div class="msg">
            <img src="../assets/msg.png" alt="" />
            <span>
              {{ sortlist?.voiceExProperty?.commentCount }}
            </span>
          </div>
        </span>
      </div>
    </div>
  </li>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  props: {
    sortlist: [Object, Array],
  },
  data() {
    return {
      pay:true,
    };
  },
  computed: {
    playCount: function () {
      return this.sortlist.voiceExProperty.replayCount > 100000000
        ? Math.round(this.sortlist.voiceExProperty.replayCount / 10000000) /
            10 +
            "亿"
        : this.sortlist.voiceExProperty.replayCount > 10000
        ? Math.round(this.sortlist.voiceExProperty.replayCount / 1000) / 10 +
          "万"
        : this.sortlist.voiceExProperty.replayCount;
    },
    durationMin: function () {
      return Math.floor(this.sortlist?.voiceInfo?.duration / 60) < 10
        ? "0" + Math.floor(this.sortlist?.voiceInfo?.duration / 60) + "'"
        : Math.floor(this.sortlist?.voiceInfo?.duration / 60) + "'";
    },
    durationSec: function () {
      return Math.floor(this.sortlist?.voiceInfo?.duration % 60) < 10
        ? "0" + Math.floor(this.sortlist?.voiceInfo?.duration % 60) + "''"
        : Math.floor(this.sortlist?.voiceInfo?.duration % 60) + "''";
    },
    ...mapState(["playvoice", "playsortlist"]),
  },
  methods: {
    ...mapMutations([
      "changeplaying",
      "changeplayvoice",
      "changesortlist",
      "changevoiceId",
    ]),
    save(){
      localStorage.setItem("playvoice", JSON.stringify(this.playvoice));
    }
  },
  // watch: {
  //   playvoice: function () {
  //     console.log(11);
  //     localStorage.setItem("playvoice", JSON.stringify(this.playvoice));
  //   },
  // },
  //   watch: {
  //  $route() {
  //     //  window.location.reload(); //监测到路由发生跳转时刷新一次页面
  //     localStorage.setItem("playvoice", JSON.stringify(this.playvoice));
  //  },
  // },
};
</script>

<style lang="less">
li.item_sort {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 12rem 16rem;
  position: relative;
  &:after {
    position: absolute;
    content: "";
    display: block;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: #dbdbdb;
    z-index: 100;
    transform: scaleY(0.5);
  }
  .img {
    display: block;
    width: 60rem;
    height: 60rem;
    margin-right: 16rem;
    transition: opacity 0.3s;
    img.iten_img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 8rem;
    }
  }
  .desc {
    flex: 1;
    width: 250rem;
    h3.nameli {
      display: block;
      width: 100%;
      text-align: left;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 14rem;
      color: rgba(0, 0, 0, 0.8);
    }
    p {
      display: block;
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      margin: 4rem 0;
      font-size: 10rem;
      text-align: left;
      color: rgba(0, 0, 0, 0.5);
    }
    .voice_tip {
      // color: #b2b2b2;
      font-size: 12rem;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      color: rgba(0, 0, 0, 0.3);
      span {
        display: block;
        margin-right: 12rem;
        div {
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            display: block;
            width: 16rem;
            padding-right: 5rem;
          }
          span {
            display: block;
          }
        }
      }
    }
  }
  .down {
    img.down {
      width: 25rem;
      display: block;
    }
  }
}
</style>>
