<template>
  <a
    class="channel"
    href="#"
    @click.prevent="
      $router.push({
        path: '/channeldetails',
        query: { id: channel.channelId },
      });time()
    "
  >
    <img class="channel_img" :src="channel?.channelUrl" alt="" />
    <h5 class="channel_title">{{ channel.channelName }}</h5>
  </a>
</template>

<script>
export default {
  props: {
    channel: Object,
  },
  methods: {
    time() {
      setTimeout(function () {
        window.location.reload();
      }, 10);
    },
  },
};
</script>

<style lang="less">
a.channel {
  height: 74.5rem;
  width: 74.5rem;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  img.channel_img {
    display: block;
    width: 55rem;
    object-fit: cover;
  }
  h5.channel_title {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
    margin-top: 5rem;
    font-size: 12rem;
    color: rgba(0, 0, 0, 0.7);
  }
  &:nth-last-of-type(3) {
    img.channel_img {
      width: 30rem;
    }
  }
}
</style>>
