<template>
  <li class="item_guess">
    <div
      class="img"
      @click="
        changeplayvoice(guesslike);
        changeplaying(!playing);
      "
    >
      <van-image width="60" height="60" lazy-load class="iten_img" :src="guesslike?.userInfo?.photo" alt="" />
      <div class="play_icon_mask">
        <div class="play_icon_inner">
          <img
            src="../assets/zhanting.png"
            alt=""
            v-if="
              playvoice?.voiceInfo?.voiceId === guesslike?.voiceInfo?.voiceId &&
              playing
            "
          />
          <img src="../assets/play_icon.png" v-else alt="" />
        </div>
      </div>
    </div>
    <div class="desc">
      <h6
        class="nameli"
        :class="{
          coo: playvoice?.voiceInfo?.voiceId === guesslike?.voiceInfo?.voiceId,
        }"
      >
        {{ guesslike?.voiceInfo.name }}
      </h6>
      <p
        :class="{
          coo: playvoice?.voiceInfo?.voiceId === guesslike?.voiceInfo?.voiceId,
        }"
      >
        {{ guesslike?.userInfo.name }}
      </p>
      <div class="data">
        <div class="voice_tip">
          <span>
            <div class="clock">
              <img src="../assets/icon_ls2.png" alt="" />
              <span> {{ durationMin }}{{ durationSec }} </span>
            </div>
          </span>
          <span>
            <div class="play_circl">
              <img src="../assets/bofang_icon.png" alt="" />
              <span>
                {{ playCount }}
              </span>
            </div>
          </span>
          <span>
            <div class="msg">
              <img src="../assets/msg.png" alt="" />
              <span>
                {{ guesslike?.voiceExProperty?.commentCount }}
              </span>
            </div>
          </span>
        </div>
        <div class="opeapp" v-if="i <= 4">打开APP收听</div>
      </div>
    </div>
  </li>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  props: {
    guesslike: [Object, Array],
    i: Number,
  },
  computed: {
    playCount: function () {
      return this.guesslike.voiceExProperty.replayCount > 100000000
        ? Math.round(this.guesslike.voiceExProperty.replayCount / 10000000) /
            10 +
            "亿"
        : this.guesslike.voiceExProperty.replayCount > 10000
        ? Math.round(this.guesslike.voiceExProperty.replayCount / 1000) / 10 +
          "万"
        : this.guesslike.voiceExProperty.replayCount;
    },
    durationMin: function () {
      return Math.floor(this.guesslike?.voiceInfo?.duration / 60) < 10
        ? "0" + Math.floor(this.guesslike?.voiceInfo?.duration / 60) + "'"
        : Math.floor(this.guesslike?.voiceInfo?.duration / 60) + "'";
    },
    durationSec: function () {
      return Math.floor(this.guesslike?.voiceInfo?.duration % 60) < 10
        ? "0" + Math.floor(this.guesslike?.voiceInfo?.duration % 60) + "''"
        : Math.floor(this.guesslike?.voiceInfo?.duration % 60) + "''";
    },
    ...mapState(["playvoice", "playsortlist", "playing"]),
  },
  methods: {
    ...mapMutations([
      "changeplaying",
      "changeplayvoice",
      "changesortlist",
      "changevoiceId",
    ]),
  },
  watch: {
    playvoice: function () {
      // console.log(11);
      localStorage.setItem("playvoice", JSON.stringify(this.playvoice));
    },
  },
};
</script>

<style lang="less">
li.item_guess {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 90rem;
  position: relative;
  border-bottom: 1rem solid #ddd;

  .img {
    display: block;
    width: 60rem;
    height: 60rem;
    margin-right: 16rem;
    transition: opacity 0.3s;
    overflow: hidden;
    position: relative;
    img.iten_img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 8rem;
    }
    .play_icon_mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 10000;
      display: flex;
      justify-content: center;
      align-items: center;
      .play_icon_inner {
        width: 40rem;
        height: 40rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          display: block;
          width: 8.5vw;
        }
      }
    }
  }
  .desc {
    flex: 1;
    width: 250rem;
    h6.nameli {
      display: block;
      width: 100%;
      text-align: left;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 14rem;
      color: #4c4c4c;
      &.coo {
        color: #fe5353;
      }
    }
    p {
      display: block;
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 10rem;
      text-align: left;
      color: #b2b2b2;
      padding: 3.5rem;
      &.coo {
        color: #fe5353;
      }
    }
    .data {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .voice_tip {
        font-size: 12rem;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        color: rgba(0, 0, 0, 0.3);
        span {
          display: block;
          margin-right: 6rem;

          div {
            display: flex;
            align-items: center;
            justify-content: center;
            img {
              display: block;
              width: 16rem;
              padding-right: 5rem;
            }
            span {
              display: block;
            }
          }
        }
      }
      .opeapp {
        font-size: 12rem;
        float: right;
        color: #fe5353;
      }
    }
  }
  .down {
    img.down {
      width: 25rem;
      display: block;
    }
  }
}
</style>>
