<template>
  <li class="commen_item">
    <img class="comm" :src="hotcomment?.photo" alt="" />
    <div class="com_desc">
      <div class="com_name">{{ hotcomment?.name }}</div>
      <div class="com_conemt">{{ hotcomment?.content }}</div>
      <div class="com_time">{{updatTime()}}</div>
    </div>
    <div class="cmd_noc">
      <div class="good">
        <img src="../assets/点赞.png" alt="" />
        <span>{{ hotcomment?.laudCnt }}</span>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    hotcomment: [Object, Array],
  },
  methods:{
    updatTime() {
      let date = new Date(
        this.hotcomment.createTime * 1000
      );
      let Y = date.getFullYear() + "-";
      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      let D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      return Y + M + D;
    },
  }
};
</script>

<style lang="less">
.commen_item {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  border-bottom: 1px solid #ddd;
  padding: 16rem 0;
  img.comm {
    display: block;
    width: 35rem;
    height: 35rem;
    border-radius: 50%;
    margin-right: 10rem;
  }
  .com_desc {
    flex: 1;
    text-align: left;
    .com_name {
      font-size: 15rem;
      color: #b2b2b2;
      padding-bottom: 8rem;
    }
    .com_conemt {
      width: 250rem;
      font-size: 15rem;
      color: #4c4c4c;
      margin: 0;
      padding: 0 0 10rem;
      word-break: break-all;
    }
    .com_time {
      font-size: 12rem;
      color: #b2b2b2;
    }
  }
  .cmd_noc {
    .good {
      font-size: 12rem;
      color: #b2b2b2;

      display: flex;

      align-items: center;
      img {
        display: block;
        width: 15rem;
      }
      span {
        margin-left: 2.5rem;
      }
    }
  }
}
</style>>
