<template>
  <div class="home">
    <section class="voice">
      <div class="voice_root">
        <div class="header_root">
          <h3 class="voice">听世界</h3>
        </div>
        <div class="channel_panel">
          <ChannelPanel
            v-for="(channel, index) in channels"
            :key="channel.channelId"
            :channel="channel"
            v-show="isOpen || index < max"
            @click.native="changeCategory(channel)"
          />
          <a
            class="channel more"
            href=""
            v-show="!isOpen && channels.length > max"
            @click.prevent="isOpen = !isOpen"
          >
            <img class="channel_img" src="../assets/more.png" alt="" />
            <h5 class="channel_title">更多</h5>
          </a>
          <a
            class="channel more"
            href=""
            v-show="isOpen && channels.length > max"
            @click.prevent="isOpen = !isOpen"
          >
            <img class="channel_img" src="../assets/收起.png" alt="" />
            <h5 class="channel_title" >收起</h5>
          </a>
        </div>
      </div>
    </section>
    <section>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <!-- <RecomendSheet /> -->
        <RecomendSheet
          v-for="(item, index) in list"
          :key="index"
          :item="item"
        />
      </van-list>
    </section>
    <VoicePlayGlo/>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import ChannelPanel from "@/components/ChannelPanel.vue";
import RecomendSheet from "@/components/RecomendSheet.vue";
import VoicePlayGlo from "@/components/VoicePlayGlo.vue";


import { mapMutations } from "vuex";


export default {
  name: "HomeView",
  components: {
    // HelloWorld
    ChannelPanel,
    RecomendSheet,
    VoicePlayGlo,

  },
  // props: ["recom"],
  data: function () {
    return {
      channels: [],
      max: 9,
      isOpen: false,
      recommends: [],

      list: [],
      loading: false,
      finished: false,
      total: 0,

      changeRecommend:null,
    };
  },
  created() {
    this.axios.get("https://m.lizhi.fm/vodapi/m5/channel").then((res) => {
      // console.log(res);
      this.channels = res.data.data.slice(1);
      this.changeRecommend= res.data.data.slice(0,1);
      // console.log(this.changeRecommend);
      this.changeRecom(this.changeRecommend)
    });
    this.axios
      .post("https://m.lizhi.fm/vodapi/playSheetRecommend/recommend")
      .then((res) => {
        console.log(res);
        this.recommends = res.data.data.cardPlayBeanList;
      });
  },
  
  //  const backWhiteList = ['Detail'];
  // beforeRouteLeave(to, from, next) {
  //   if (this.backWhiteList.includes(to.name)) {
  //     from.meta.keepAlive = true;
  //   } else {
  //     from.meta.keepAlive = false;
  //   }
  //   next();
  // },

  methods: {
    onLoad() {
      // 异步更新数据
      // this.loading = true;
      this.total += 6;
      this.axios
        .post("https://m.lizhi.fm/vodapi/playSheetRecommend/recommend")
        .then((res) => {
          console.log(res);
          // this.recommend = res.data.data;
          res.data.data.cardPlayBeanList.forEach((item) => {
            this.list.push(item);
          });
          if (this.list.length > this.total) {
            this.finished = true;
          }
          this.loading = false;
        });
    },
    ...mapMutations(["changeRecom","changeCategory"]),
  },
};
</script>
