<template>
  <div
    class="userplaylist"
    @click.prevent="
      $router.push({
        path: '/voicesheet',
        query: { id: userplaylist?.id },
      })
    "
  >
    <div class="img">
      <van-image width="60" height="60" lazy-load class="iten_img" :src="userplaylist?.cover" alt="" />
    </div>
    <div class="descplay">
      <h6 class="nameli">
        {{ userplaylist?.name }}
      </h6>
      <div class="voiceplay_tip">
        <span> {{ updatTime() }} </span>
        <span>
          <div class="msg">
            <img src="../assets/musiclist.png" alt="" />
            <p class="playlist">{{ userplaylist?.voiceNum }}</p>
          </div>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
// import VoiceList from "@/components/VoiceList.vue";

export default {
  // components: {
  //   VoiceList,
  // },
  props: {
    userplaylist: [Object, Array],
  },
  methods: {
    updatTime() {
      let date = new Date(this.userplaylist?.createTime * 1000);
      let Y = date.getFullYear() + "-";
      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      let D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      return Y + M + D;
    },
  },
};
</script>

<style lang="less">
.userplaylist {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90rem;
  position: relative;

  &:after {
    position: absolute;
    content: "";
    display: block;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: #dbdbdb;
    z-index: 100;
    transform: scaleY(0.5);
  }
  .img {
    display: block;
    width: 60rem;
    height: 60rem;
    margin-right: 10rem;
    overflow: hidden;
    position: relative;
    box-shadow: 3px 3px 10px rgb(0 0 0 / 5%);
    img.iten_img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 6rem;
    }
    .play_icon_mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 10000;
      display: flex;
      justify-content: center;
      align-items: center;
      .play_icon_inner {
        width: 40rem;
        height: 40rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          display: block;
          width: 8.5vw;
        }
      }
    }
  }
  .descplay {
    flex: 1;
    width: 250rem;
    h6.nameli {
      display: block;
      width: 100%;
      text-align: left;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 15rem;
      color: #333;
      padding-bottom: 10rem;
    }
    .voiceplay_tip {
      font-size: 14rem;
      text-align: left;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      color: rgba(0, 0, 0, 0.3);
      span {
        display: block;
        height: 20rem;
        margin-right: 10rem;
        div.msg {
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            display: inline-block;
            width: 20rem;
          }
          p.playlist {
            font-size: 14rem;
            display: inline-block;
            padding: 0;
          }
        }
      }
    }
  }
}
</style>>
