<template>
  <div class="personalpage">
    <div class="person_info">
      <div class="per_cover">
        <van-image round
          :src="
            userinfo && `${userinfo?.cdnPortrait}${userinfo?.user?.portrait}`
          "
          alt=""
        />
      </div>
      <div class="per_name">
        <h3 class="pername">{{ userinfo?.user?.name }}</h3>
        <div class="tag">
          <img
            class="sex"
            v-if="userinfo?.user?.gender === 1"
            src="../assets/man.png"
            alt=""
          />
          <img
            class="sex"
            v-if="userinfo?.user?.gender === 0"
            src="../assets/men.png"
            alt=""
          />
          <!-- 0男 1女 -->
        </div>
        <div class="watch">
          <span class="w2">
            <img src="../assets/加.png" alt="" />
            <p>关注</p>
          </span>
        </div>
      </div>
      <div class="per_des">
        <span>FM {{ userinfo?.user?.band }}</span>
        <span>{{ userinfo?.user?.fansCount }}粉丝</span>
        <span>{{ playCount(userinfo?.radio?.play_cnt) }}播放</span>
      </div>
      <div class="per_detail">
        <div class="desc_com">
          <div class="conent" :class="{ isAl: isAl }">
            {{ userinfo?.user?.signature }}
          </div>
          <span class="ic" v-if=" userinfo?.user?.signature">
            <img
              src="../assets/zhankai.png"
              @click="isAl = true"
              v-if="!isAl"
              alt=""
            />
            <img
              src="../assets/shouqi.png"
              @click="isAl = false"
              v-else
              alt=""
            />
          </span>
        </div>
      </div>
    </div>
    <div class="per_voice_list">
      <div class="per_tab_title">
        <div
          class="tab_item"
          :class="{ active: isShow }"
          @click.stop="isShow = true"
        >
          热门声音
        </div>
        <div
          class="tab_item"
          :class="{ active: !isShow }"
          @click.stop="isShow = false"
        >
          播单({{ userplaylists?.length }})
        </div>
      </div>
      <div class="list_titl" v-if="isShow">
        <div
          class="playicon"
          v-if="isShow && userhotvoics?.length ? true : false"
        >
          <img src="../assets/bofangall.png" alt="" />
          <span>全部播放</span>
        </div>
      </div>
      <div class="voli">
        <div class="per_voic_list" v-if="isShow">
          <UserHotList
            v-for="(userhotvoic, index) in userhotvoics"
            :key="index"
            :userhotvoic="userhotvoic"
          />
        </div>
        <div class="per_voic_list" v-if="!isShow">
          <UserPlaylist
            v-for="(userplaylist, index) in userplaylists"
            :key="index"
            :userplaylist="userplaylist"
          />
        </div>
        <div class="hotaeea">
          <div
            class="cmd_hoc"
            v-if="isShow && userhotvoics?.length ? true : false"
          >
            打开APP收听更多热门声音
          </div>
        </div>
      </div>
    </div>
    <div
      class="per_voice_list"
      v-if="isShow && alluserlist?.length ? true : false"
    >
      <div class="per_tab_title">
        <div class="tit">全部声音({{ allistotal?.total }})</div>
        <!-- <div
          class="tab_item"
          :class="{ active: !isShow }"
          @click.stop="isShow = false"
        >
          播单({{ userplaylists?.length }})
        </div> -->
      </div>
      <!-- <div class="list_titl" v-if="isShow">
        <div class="playicon">
          <img src="../assets/bofangall.png" alt="" />
          <span>全部播放</span>
        </div>
      </div> -->
      <div class="voli">
        <div class="per_voic_list" v-if="isShow">
          <van-list v-model="loading" :finished="finished" @load="onLoad">
            <AllUserList
              v-for="(allaudio, index) in alluserlist"
              :key="index"
              :allaudio="allaudio"
            />
          </van-list>
        </div>
      </div>
      <!-- <div class="cmd_hoc" v-if="isShow && userhotvoics?.length ? true : false">
        打开APP收听更多热门声音
      </div> -->
      <!-- </div> -->
    </div>
    <div class="to_top" v-if="topshallow" @click="backtop()">
      <div class="overline">
        <img src="../assets/top.png" alt="" />
      </div>
    </div>
    <div class="dobottom">别拉了，到底啦o(╥﹏╥)o</div>
    <div
      class="empty"
      v-if="
        userhotvoics?.length
          ? false
          : true && userplaylists?.length
          ? false
          : true && alluserlist?.length
          ? false
          : true
      "
    >
      该主播尚未发布内容
    </div>
  </div>
</template>

<script>
import UserHotList from "@/components/UserHotList.vue";
import UserPlaylist from "@/components/UserPlaylist.vue";
import AllUserList from "@/components/AllUserList.vue";

export default {
  components: {
    UserHotList,
    UserPlaylist,
    AllUserList,
  },
  data: function () {
    return {
      userinfo: null,
      userhotvoics: null,
      userplaylists: null,
      alluserlist: null,
      pagn: 1,
      isShow: true,
      allistotal: 0,

      topshallow: false,
      scrollTop: 0,

      list: [],
      loading: false,
      finished: false,
      totalelit: 0,

      isAl: false,
    };
  },
  // computed: {
  // },
  mounted() {
    // 开启滚动监听
    window.addEventListener("scroll", this.scrollToTop);
    // console.log(11);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },
  methods: {
    getUserInfo: function (id) {
      this.axios.get(`https://m.lizhi.fm/api/user/info/${id}`).then((res) => {
        // console.log(res);
        this.userinfo = res.data;
      });
    },
    getUserHotVoic: function (id) {
      this.axios
        .get(`https://m.lizhi.fm/vodapi/voice/hot/${id}`)
        .then((res) => {
          // console.log(res);
          this.userhotvoics = res.data.data;
        });
    },
    getUserPlaylist: function (id) {
      this.axios
        .get("https://m.lizhi.fm/vodapi/playsheet/listdata", {
          params: { userId: id },
        })
        .then((res) => {
          console.log(res);
          this.userplaylists = res.data.data;
        });
    },
    getAllUserList: function (id) {
      this.axios
        .get(`https://m.lizhi.fm/api/user/audios/${id}/${this.pagn}`)
        .then((res) => {
          // console.log(res);
          this.allistotal = res.data;
          let rowslist = res.data.audios;
          // this.total += 10;
          this.loading = false;
          // this.totale += rows.length;
          this.totalelit = rowslist.length;
          // this.sortlists=res.data.data;
          if (rowslist == null || rowslist.length === 0) {
            // 加载结束
            this.finished = true;
            return;
          }
          this.alluserlist = this.alluserlist
            ? [...this.alluserlist, ...rowslist]
            : rowslist;
          if (this.alluserlist.length < this.totale) {
            this.finished = true;
          }
          // this.alluserlist = res.data;
        });
    },
    onLoad() {
      this.pagn++;
      this.getAllUserList(this.$route.query.id);
      // this.totale += 20;
    },

    playCount: function (playCount) {
      return playCount > 100000000
        ? Math.round(playCount / 10000000) / 10 + "亿"
        : playCount > 10000
        ? Math.round(playCount / 1000) / 10 + "万"
        : playCount;
    },
    scrollToTop() {
      this.scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      // console.log(this.scrollTop);
      if (this.scrollTop >= 180) {
        this.topshallow = true;
        // document.querySelector('.van-nav-bar').style.display = 'none'
      } else {
        this.topshallow = false;

        // document.querySelector('.van-nav-bar').style.display = 'block'
      }
    },
    backtop() {
      // console.log(11);
      // document.documentElement.scrollTop = 0;
      const timeTop = setInterval(() => {
        document.documentElement.scrollTop = this.scrollTop -= 30;
        if (this.scrollTop <= 0) {
          clearInterval(timeTop);
        }
      }, 10);
    },
  },
  created: function () {
    this.getUserInfo(this.$route.query.id);
    this.getUserHotVoic(this.$route.query.id);
    this.getUserPlaylist(this.$route.query.id);
    this.getAllUserList(this.$route.query.id);
  },
  beforeRouteUpdate(to, from, next) {
    this.getUserInfo(to.query.id);
    this.getUserHotVoic(to.query.id);
    this.getUserPlaylist(to.query.id);
    this.getAllUserList(to.query.id);
    next();
  },
};
</script>

<style lang="less">
