        background: #fe5353;
        color: #fff;
        font-weight: 700;
        box-shadow: 0 2px 4px 0 rgb(255 98 98 / 30%);
        span.w2 {
          display: flex;
          justify-content: center;
          align-items: center;
          img {
            width: 10rem;
            display: block;
            margin-right: 3rem;
          }
          p {
          }
        }
      }
    }
