<template>
  <div class="playdeta">
    <div class="player">
      <div class="paper">
        <!-- <img class="auto" :src="newplayvoice?.cover" alt="" /> -->
        <van-image
          width="226"
          height="226"
          lazy-load
          class="auto"
          :src="
            newplayvoice?.cover
              ? newplayvoice?.cover
              : userinfo?.userVoice?.voiceInfo?.imageUrl
          "
          alt=""
          v-if="newplayvoice?.voiceId"
        />
        <img
          class="auto"
          :src="
            newplayvoice?.voiceInfo?.imageUrl
              ? newplayvoice?.voiceInfo?.imageUrl
              : userinfo?.userVoice?.voiceInfo?.imageUrl
          "
          alt=""
          v-else
        />
        <div class="btn_g">
          <div class="pre" @click="changeVoice()">
            <img src="../assets/skip_previous.png" alt="" />
          </div>
          <div
            class="start"
            @click="
              newplay = !newplay;
              changeplaying(!playing);
              changeplayvoice(
                newplayvoice ? newplayvoice : userinfo?.userVoice
              );
            "
          >
            <!--test() changeplayvoice(newplayvoice); -->
            <img src="../assets/暂停.png" alt="" v-if="playing" />
            <img src="../assets/目标回顾.png" alt="" v-else />
          </div>
          <div class="next" @click="changeVoice('next')">
            <img src="../assets/skip_next.png" alt="" />
          </div>
        </div>
      </div>
      <!-- <h1 class="song_name">{{ newplayvoice?.name }}</h1> -->
      <h1 class="song_name" v-if="newplayvoice?.voiceId">
        {{ newplayvoice?.name }}
      </h1>
      <h1 class="song_name" v-else>{{ newplayvoice?.voiceInfo.name }}</h1>
      <div class="voice_controller">
        <ProgressBar :newplayvoice="newplayvoice" :userinfo="userinfo" />
      </div>
      <div class="opt_compent">
        <div class="list">
          <div class="opt_item" @click.stop="reducecurrentTime(n)">
            <img src="../assets/houtui.png" alt="" />
          </div>
          <div class="opt_item">
            <img src="../assets/xiazai.png" alt="" />
          </div>
          <div class="opt_item" @click.stop="changespeed(2)">
            <img src="../assets/beisu.png" alt="" />
          </div>
          <div class="opt_item" @click.stop="addcurrentTime(n)">
            <img src="../assets/qianjin.png" alt="" />
          </div>
        </div>
      </div>
      <div class="down_btn">打开APP收藏下载</div>
    </div>
    <div class="userinfo">
      <div class="title">声音详情</div>
      <div class="userdesc">
        <div class="warp">
          <img
            class="avtal"
            :src="userinfo?.userVoice?.userInfo?.photo"
            alt=""
          />
          <div class="userin">
            <div class="name">{{ userinfo?.userVoice?.userInfo?.name }}</div>
            <div class="detail">
              FM {{ userinfo?.userVoice?.userInfo?.band }}
              {{ userinfo?.userVoice?.userInfo?.fansCnt }}粉丝
            </div>
          </div>
        </div>
        <div class="watch">
          <span class="w2">
            <img src="../assets/加.png" alt="" />
            <p>关注</p>
          </span>
        </div>
      </div>
      <div class="voice_in">
        <h3 class="nam">{{ userinfo?.userVoice?.voiceInfo?.name }}</h3>
        <div class="info">{{ updatTime() }} 播放 {{ playCountt }}</div>
        <strong class="detail">
          <div class="desc_com" :class="{ isAll: isAll }">
            {{ userinfo?.userVoice?.voiceDetailProperty?.text }}
          </div>
          <span
            class="ic"
            v-if="userinfo?.userVoice?.voiceDetailProperty?.text"
          >
            <img
              src="../assets/zhankai.png"
              @click="isAll = true"
              v-if="!isAll"
              alt=""
            />
            <img
              src="../assets/shouqi.png"
              @click="isAll = false"
              v-else
              alt=""
            />
          </span>
        </strong>
      </div>
    </div>
    <div class="player_similar">
      <div class="title">
        <div class="nav_ite">猜你喜欢</div>
      </div>
      <ul class="com_list">
        <GuessLikeVoic
          v-for="(guesslike, i) in guesslikevoic"
          :key="i"
          :guesslike="guesslike"
          :i="i"
        />
      </ul>
    </div>
    <div class="player_rank">
      <div class="titl">大家都在听</div>
      <ul class="ranklist">
        <EveryListen
          v-for="(everylist, i) in everylisten"
          :key="i"
          :everylist="everylist"
        />
        <div class="down_btn">打开APP 收听更多热播声音</div>
      </ul>
    </div>
    <div class="player_hotcomments" v-if="com">
      <div class="titl">热门评论</div>
      <ul class="comments_list">
        <HotComments
          v-for="(hotcomment, i) in hotcomments"
          :key="i"
          :hotcomment="hotcomment"
        />
      </ul>
      <div class="cmd_hoc">
        <div class="go_tip">打开APP查看全部评论 ></div>
      </div>
    </div>
    <div class="space"></div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import ProgressBar from "@/components/ProgressBar.vue";
import GuessLikeVoic from "@/components/GuessLikeVoic.vue";
import EveryListen from "@/components/EveryListen.vue";
import HotComments from "@/components/HotComments.vue";

export default {
  components: {
    // MusicLyric,
    ProgressBar,
    GuessLikeVoic,
    EveryListen,
    HotComments,
  },
  data() {
    return {
      newplayvoice:
        JSON.parse(localStorage.getItem("playvoice")) || this.playvoice,
      newplay: true,
      n: 15,
      newplaysor:
        JSON.parse(localStorage.getItem("playsortlist")) || this.playsortlist,
      // play:JSON.parse(localStorage.getItem("playing")),
      userinfo: null,
      guesslikevoic: null,
      everylisten: null,
      hotcomments: null,
      com: null,
      newplayvoicelist:
        JSON.parse(localStorage.getItem("voicelist")) || this.voicelist,
      // pay:true,
      isAll: false,
    };
  },

  computed: {
    ...mapState([
      "playvoice",
      "playing",
      "voicelist",
      "playsortlist",
      "voiceId",
      "currentTime",
      "voicelist",
    ]),
    playCountt: function () {
      return this.userinfo?.userVoice?.voiceExProperty?.replayCount > 100000000
        ? Math.round(
            this.userinfo?.userVoice?.voiceExProperty?.replayCount / 10000000
          ) /
            10 +
            "亿"
        : this.userinfo?.userVoice?.voiceExProperty?.replayCount > 10000
        ? Math.round(
            this.userinfo?.userVoice?.voiceExProperty?.replayCount / 1000
          ) /
            10 +
          "万"
        : this.userinfo?.userVoice?.voiceExProperty?.replayCount;
    },
  },
  created() {
    console.log(this.$route.query.id);
    this.getUserInfo(
      this.newplayvoice?.voiceId
        ? this.newplayvoice?.voiceId
        : this.newplayvoice?.voiceInfo?.voiceId
        ? this.newplayvoice?.voiceInfo?.voiceId
        : this.$route.query.id
    );
    this.getGuesslike(
      this.newplayvoice?.voiceId
        ? this.newplayvoice?.voiceId
        : this.newplayvoice?.voiceInfo?.voiceId
        ? this.newplayvoice?.voiceInfo?.voiceId
        : this.$route.query.id
    );
    this.gethHotcomments(
      this.newplayvoice?.voiceId
        ? this.newplayvoice?.voiceId
        : this.newplayvoice?.voiceInfo?.voiceId
        ? this.newplayvoice?.voiceInfo?.voiceId
        : this.$route.query.id
    );
    // this.getUserInfo(this.$route.query.id);
    // this.getGuesslike(this.$route.query.id);
    // this.gethHotcomments(this.$route.query.id);
    this.axios.get(`https://m.lizhi.fm/vodapi/voice/rank/voice`).then((res) => {
      this.everylisten = res.data.data;
      // this.changeTablists(this.tablistes);
    });
  },

  methods: {
    ...mapMutations([
      "changeplaying",
      "changeplayvoice",
      "changecurrentTime",
      "reducecurrentTime",
      "addcurrentTime",
      "changeVoice",
      "changespeed",
    ]),
    getUserInfo(id) {
      this.axios
        .get(`https://m.lizhi.fm/vodapi/voice/info/${id}`)
        .then((res) => {
          this.userinfo = res.data.data;
          // this.changeTablists(this.tablistes);
        });
    },
    getGuesslike(id) {
      this.axios
        .get(`https://m.lizhi.fm/vodapi/voice/similar/${id}`)
        .then((res) => {
          this.guesslikevoic = res.data.data;
          // this.changeTablists(this.tablistes);
        });
    },
    gethHotcomments(id) {
      this.axios
        .get(`https://m.lizhi.fm/vodapi/voice/comment/${id}`)
        .then((res) => {
          this.hotcomments = res.data.data;
          this.com = res.data.data.length ? true : false;
          // this.changeTablists(this.tablistes);
        });
    },
    // nextvoice() {
    //   // var index = this.newplayvoicelist.filter(
    //   //   (o) => o.voiceId === this.playvoice.voiceId
    //   // );
    //   // var n=this.newplayvoicelist.findIndex(index)
    //   // console.log(index);
    //   var n = this.newplayvoicelist.findIndex(
    //     (o) => o.voiceId === this.playvoice.voiceId
    //   );
    //   console.log(n);
    // },
    updatTime() {
      let date = new Date(
        this.userinfo?.userVoice?.voiceInfo?.createTime * 1000
      );
      let Y = date.getFullYear() + "-";
      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      let D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      return Y + M + D;
    },
    // changroutrid() {
    //   console.log(11);
    //   // this.$router.replace({
    //   //   query: this.newplayvoice.voiceId,
    //   // });
    //   // this.$router.replace({ name: 'Orders', params: { tabId: index } })
    // },
  },
  watch: {
    playing: function () {
      // console.log(11);
      localStorage.setItem("playsortlist", JSON.stringify(this.playsortlist));
    },
    playvoice(playvoice, o) {
      console.log(o);
      // console.log(11);
      // var netplay=this.playing
      // if (!netplay) {
      // this.changeplaying(this.playing);
      // }
      // else {
      //   this.changeplaying(!netplay);
      // }
      this.newplayvoice = playvoice;
      this.getUserInfo(
        this.newplayvoice?.voiceId
          ? this.newplayvoice?.voiceId
          : this.newplayvoice?.voiceInfo?.voiceId
      );
      this.getGuesslike(
        this.newplayvoice?.voiceId
          ? this.newplayvoice?.voiceId
          : this.newplayvoice?.voiceInfo?.voiceId
      );
      this.gethHotcomments(
        this.newplayvoice?.voiceId
          ? this.newplayvoice?.voiceId
          : this.newplayvoice?.voiceInfo?.voiceId
      );
      if (o?.voiceId !== this.newplayvoice.voiceId) {
        this.$router.replace({
          query: {
            id: this.newplayvoice.voiceId,
          },
        });
      }

      // this.changeplayvoice(this.newplayvoice);
      // localStorage.setItem("playvoice", JSON.stringify(playvoice));
    },
  },
};
</script>

