<template>
  <li class="item">
    <div class="img">
      <van-image width="60" height="60" lazy-load class="iten_img" :src="voices?.cover" alt="" />
      <div class="play_icon_mask">
        <div
          class="play_icon_inner"
          @click="
            changeplayvoice(voices);
            changeplaying(!playing);
          "
        >
          <img
            src="../assets/zhanting.png"
            alt=""
            v-if="playvoice?.voiceId === voices?.voiceId && playing"
          />
          <img v-else src="../assets/play_icon.png" alt="" />
        </div>
      </div>
    </div>
    <div
      class="desc"
      @click.prevent="
        $router.push({
          path: '/voiceplaydeta',
          query: { id: voices.voiceId },
        });
        changeplayvoice(voices);
        savevoice();
        changeplaying(paying);
      "
    >
      <h3
        class="name"
        :class="{ colo: playvoice?.voiceId === voices?.voiceId }"
      >
        <p>{{ voices?.name }}</p>
      </h3>
      <div class="voice_tip">
        <span>
          <div class="clock">
            <img src="../assets/icon_ls2.png" alt="" />
            <span> {{ durationMin }}{{ durationSec }} </span>
          </div>
        </span>
        <span>
          <div class="play_circl">
            <img src="../assets/bofang_icon.png" alt="" />
            <span>
              {{ playCount }}
            </span>
          </div>
        </span>
        <span>
          <div class="msg">
            <img src="../assets/msg.png" alt="" />
            <span>
              {{ voices?.commentCount }}
            </span>
          </div>
        </span>
      </div>
    </div>
    <div class="down">
      <img class="down" src="../assets/download.png" alt="" />
    </div>
  </li>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  props: {
    voices: [Object, Array],
  },
  data() {
    return {
      paying: true,
    };
  },
  computed: {
    playCount: function () {
      return this.voices.playCount > 100000000
        ? Math.round(this.voices.playCount / 10000000) / 10 + "亿"
        : this.voices.playCount > 10000
        ? Math.round(this.voices.playCount / 1000) / 10 + "万"
        : this.voices.playCount;
    },
    durationMin: function () {
      return Math.floor(this.voices?.duration / 60) < 10
        ? "0" + Math.floor(this.voices?.duration / 60) + "'"
        : Math.floor(this.voices?.duration / 60) + "'";
    },
    durationSec: function () {
      return Math.floor(this.voices?.duration % 60) < 10
        ? "0" + Math.floor(this.voices?.duration % 60) + "''"
        : Math.floor(this.voices?.duration % 60) + "''";
    },
    ...mapState(["playvoice", "playsortlist", "playing"]),
  },
  methods: {
    ...mapMutations([
      "changeplaying",
      "changeplayvoice",
      "changesortlist",
      "changevoiceId",
    ]),
    savevoice() {
      localStorage.setItem("playvoice", JSON.stringify(this.playvoice));
    },
  },
};
</script>

<style lang="less">
li.item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 15rem 0;
  position: relative;
  &:after {
    position: absolute;
    content: "";
    display: block;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: #dbdbdb;
    z-index: 100;
    transform: scaleY(0.5);
  }
  &:first-of-type {
    padding-top: 5rem;
  }
  .img {
    display: block;
    width: 60rem;
    height: 60rem;
    margin-right: 10rem;
    border-radius: 6rem;
    overflow: hidden;
    position: relative;
    img.iten_img {
      display: block;
      width: 100%;
      height: 100%;
    }
    .play_icon_mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 10000;
      display: flex;
      justify-content: center;
      align-items: center;
      .play_icon_inner {
        width: 40rem;
        height: 40rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          display: block;
          width: 8.5vw;
        }
      }
    }
  }
  .desc {
    flex: 1;
    h3.name {
      text-align: left;
      font-size: 14rem;
      font-weight: 400;
      color: #000;
      padding-bottom: 5rem;
      // width: 246rem;
      // overflow: hidden;
      // white-space: nowrap;
      // text-overflow: ellipsis;

      &.colo {
        color: #fe5656;
      }
      p {
        margin: 14rem 0;
      }
    }
    .voice_tip {
      color: #b2b2b2;
      font-size: 12rem;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      span {
        display: block;
        margin-right: 12rem;
        div {
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            display: block;
            width: 16rem;
            padding-right: 5rem;
          }
          span {
            display: block;
          }
        }
      }
    }
  }
  .down {
    img.down {
      width: 25rem;
      display: block;
    }
  }
}
</style>>
