<template>
  <div class="item_pervoic">
    <div class="img">
      <van-image width="60" height="60" lazy-load class="iten_img" :src="userhotvoic?.voiceInfo?.imageUrl" alt="" />
      <div
        class="play_icon_mask"
        @click="
          changeplayvoice(userhotvoic);
          changeplaying(!playing);
        "
      >
        <div class="play_icon_inner">
          <img
            src="../assets/zhanting.png"
            alt=""
            v-if="
              playvoice?.voiceInfo?.voiceId === userhotvoic?.voiceInfo?.voiceId &&
              playing
            "
          />
          <img v-else src="../assets/play_icon.png" alt="" />
        </div>
      </div>
    </div>
    <div class="desc" @click.prevent="
      $router.push({
        path: '/voiceplaydeta',
        query: { id: userhotvoic?.voiceInfo?.voiceId },
      });
      changeplayvoice(userhotvoic);
      changeplaying(!playing);
    ">
      <h6 class="nameli">
        {{ userhotvoic?.voiceInfo?.name }}
      </h6>
      <div class="voice_tip">
        <span> {{ durationMin }}{{ durationSec }} </span>
        <span> {{ playCount }} 播放 </span>
        <span> {{ userhotvoic?.voiceExProperty?.commentCount }} 评论 </span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  props: {
    userhotvoic: [Object, Array],
  },
  computed: {
    ...mapState(["playvoice", "playing"]),
    playCount: function () {
      return this.userhotvoic?.voiceExProperty?.replayCount > 100000000
        ? Math.round(
            this.userhotvoic?.voiceExProperty?.replayCount / 10000000
          ) /
            10 +
            "亿"
        : this.userhotvoic?.voiceExProperty?.replayCount > 10000
        ? Math.round(this.userhotvoic?.voiceExProperty?.replayCount / 1000) /
            10 +
          "万"
        : this.userhotvoic?.voiceExProperty?.replayCount;
    },
    durationMin: function () {
      return Math.floor(this.userhotvoic?.voiceInfo?.duration / 60) < 10
        ? "0" + Math.floor(this.userhotvoic?.voiceInfo?.duration / 60) + "'"
        : Math.floor(this.userhotvoic?.voiceInfo?.duration / 60) + "'";
    },
    durationSec: function () {
      return Math.floor(this.userhotvoic?.voiceInfo?.duration % 60) < 10
        ? "0" + Math.floor(this.userhotvoic?.voiceInfo?.duration % 60) + "''"
        : Math.floor(this.userhotvoic?.voiceInfo?.duration % 60) + "''";
    },
  },
  methods: {
    ...mapMutations(["changeplaying", "changeplayvoice"]),
  },
};
</script>

<style lang="less">
.item_pervoic {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90rem;
  position: relative;
  &:after {
    position: absolute;
    content: "";
    display: block;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: #dbdbdb;
    z-index: 100;
    transform: scaleY(0.5);
  }
  .img {
    display: block;
    width: 60rem;
    height: 60rem;
    margin-right: 10rem;
    overflow: hidden;
    position: relative;
    box-shadow: 3px 3px 10px rgb(0 0 0 / 5%);
    img.iten_img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 6rem;
    }
    .play_icon_mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 10000;
      display: flex;
      justify-content: center;
      align-items: center;
      .play_icon_inner {
        width: 40rem;
        height: 40rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          display: block;
          width: 8.5vw;
        }
      }
    }
  }
  .desc {
    flex: 1;
    width: 250rem;
    h6.nameli {
      display: block;
      width: 100%;
      text-align: left;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 15rem;
      color: #333;
      padding-bottom: 9rem;
    }
    .voice_tip {
      font-size: 14rem;
      text-align: left;
      color: rgba(0, 0, 0, 0.3);
      span {
        height: 20rem;
        display: inline-block;
        margin-right: 10rem;
      }
    }
  }
}
</style>>
