<template>
  <div
    class="play_global"
    v-if="playvoice && playvoice.voiceId"
    @click.prevent="
      $router.push({
        path: '/voiceplaydeta',
        query: { id: playvoice?.voiceId },
      })
    "
  >
    <img class="progress" src="../assets/play_gola.png" alt="" />
    <img
      class="play_img"
      :src="playvoice?.cover"
      alt=""
      :class="{ playing: playing }"
    />
    <canvas id="canvas" ref="canvas" width="62" height="62"></canvas>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  computed: {
    ...mapState(["playvoice", "playing", "currentTime", "duration"]),
  },
  methods: {
    upadebar(val) {
      var canvas = this.$refs.canvas;
      var bar = canvas.getContext("2d");
      bar.clearRect(0, 0, 62, 62);
      bar.lineCap = "round";
      bar.strokeStyle = "#f51416";
      bar.lineWidth = 2;
      bar.beginPath();
      bar.arc(30, 30, 29, -Math.PI / 2, -Math.PI / 2 + 2 * val * Math.PI);
      bar.stroke();

    },

  },

  watch: {
    playing: function () {
      localStorage.setItem("playvoice", JSON.stringify(this.playvoice));
    },
    currentTime(n) {
      if (this.playvoice && this.playvoice.voiceId) {
         this.upadebar((n / this.duration) * 1);
      }   
    },
  },
};
</script>

<style lang="less">
.play_global {
  position: fixed;
  bottom: 10rem;
  right: 10rem;
  width: 61.5rem;
  height: 61.5rem;
  z-index: 999999;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 2px 2px 10px 0 rgb(0 0 0 / 30%);
  img.progress {
    display: block;
    width: 100%;
  }
  @keyframes rotate {
    form {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  img.play_img {
    display: block;
    position: absolute;
    top: 3%;
    left: 3%;
    width: 94%;
    height: 94%;
    border-radius: 100%;
    overflow: hidden;
    animation: rotate 8s linear infinite;
    animation-play-state: paused;
    &.playing {
      animation-play-state: running;
    }
  }
  canvas#canvas {
    position: absolute;
    top: 1rem;
    left: 1rem;
    display: inline-block;
    width: 62rem;
    height: 62rem;
    text-align: center;
  }
}
</style>>

