import Vue from 'vue';
import Vuex from 'vuex';


// import Vant from 'vant';
// import 'vant/lib/index.css';

// Vue.use(Vant);

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    recom: null,
    category: null,
    tablists: null,
    classification: null,
    playvoice: null,
    playing: false,
    duration: 0,
    currentTime: 0,
    voicelist: null,
    playsortlist: null,
    voiceId: null,
    classort: null,

    speed:1,
  },
  getters: {
  },
  mutations: {
    changeRecom(state, recom) {
      // console.log(recom);
      state.recom = recom;
    },
    changeCategory(state, category) {
      state.category = category;
    },
    changeTablists(state, tablists) {
      state.tablists = tablists;
    },
    changeClasslists(state, classification) {
      state.classification = classification;
    },
    changeplayvoice(state, playvoice) {
      state.playvoice = playvoice;
      return  localStorage.setItem("playvoice", JSON.stringify(state.playvoice));
    },
    changevoicelist(state, voicelist) {
      state.voicelist = voicelist;
      return localStorage.setItem("voicelist", JSON.stringify(state.voicelist));
      // console.log(state.playvoice.voiceId);
    },
    changeplaying(state, playing) {
      state.playing = playing;
    },
    changeduration(state, duration) {
      // console.log(duration);
      state.duration = duration;
    },
    getcurrentTime(state, currentTime) {
      // console.log(currentTime);
      state.currentTime = Number(currentTime);
      // return document.querySelector('audio').currentTime=state.currentTime;
    },

    changecurrentTime(state, currentTime) {
      // console.log(currentTime);
      state.currentTime = Number(currentTime);
      return document.querySelector('audio').currentTime = state.currentTime;
    },
    reducecurrentTime(state, n) {
      // state.currentTime -= n < 0 ? 0 : state.currentTime-=n;
      state.currentTime -= n;
      if (state.currentTime < 0) {
        state.currentTime = 0;
      }
      return document.querySelector('audio').currentTime = state.currentTime;

    },
    addcurrentTime(state, n) {

      // state.currentTime += n>state.duration?state.duration:state.currentTime;
      state.currentTime += n;
      if (state.currentTime > state.duration) {
        state.currentTime = state.duration;

      }
      return document.querySelector('audio').currentTime = state.currentTime;

    },
    changesortlist(state, sortlist) {
      // console.log(recom);
      state.playsortlist = sortlist;
    },
    changevoiceId(state, voiceId) {
      // console.log(recom);
      state.voiceId = voiceId;
    },
    changeclassort(state, classort) {
      // console.log(recom);
      state.classort = classort;
    },

    changeVoice: function (state, direction) {
      // console.log(11);
      var newvoicelist = state.voicelist ? state.voicelist : JSON.parse(localStorage.getItem("voicelist"));
      var nplayvoice = state.playvoice ? state.playvoice : JSON.parse(localStorage.getItem("playvoice"));
      var currentVoiceIndex = newvoicelist.findIndex(
        (o) => o.voiceId === nplayvoice.voiceId
      );
      if (direction === "next") {
        state.playvoice =
          currentVoiceIndex === newvoicelist.length - 1
            ? newvoicelist[0]
            : newvoicelist[currentVoiceIndex + 1];
      } else {
        state.playvoice =
          currentVoiceIndex === 0
            ? newvoicelist[newvoicelist.length - 1]
            : newvoicelist[currentVoiceIndex - 1];
      }
    },

    changespeed(state, speed) {
      // console.log(recom);
      state.speed = speed;
    },




  },
  actions: {
  },
  modules: {
  }
});
